.wrapper {
    // position: fixed;
    // top: 0;
    // right: 0;
    // left: 0;
    z-index: 1;
    --transition-curve: cubic-bezier(0.05, 0, 0.2, 1);
    height: 72px;
    background-color: rgb(255, 255, 255);
    max-width: 100vw;
    box-shadow: rgb(229 232 235) 0px 1px 0px 0px;
    transition: top 0.5s var(--transition-curve), background-color 0.2s var(--transition-curve),
        box-shadow 0.2s var(--transition-curve), color 0.2s var(--transition-curve);
}

.inner {
    height: 100%;
    padding: 0px 32px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo-link {
    display: flex;
    align-items: center;
    padding-right: 48px;
}

.logo-opensea {
    height: 40px;
}

.brand-name {
    margin-left: 10px;
    margin-top: 4px;
}

// fresnel-container
.fresnel-container {
    width: 552px;
    display: flex;
}

.fresnel-list {
    display: flex;
    align-items: center;
    li {
        height: 72px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-weight: 600;
        font-size: 16px;

        // &:hover {
        //     opacity: 0.8;
        // }

        .fresnel-item {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 0 16px;
            min-width: 64px;
            height: 72px;
            cursor: pointer;
        }
        .item-icon {
            font-size: 25px;
            color: var(--text-color);
        }
    }
}
